*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.min-hight{
  height: 100vh;
}

.cursor-pointer{
  cursor: pointer;
}

.show{
  display: flex;
  z-index: 1;
}

.select-none{
  user-select: none;
}

.hide{
  display: none !important;
  z-index: 0;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: transparent;
  border: white solid 0.5px;
}