.nav-item{
  transition: 0.25s;  
}

.nav-item:hover{
  text-shadow: 0px 0px 8px rgba(255, 255, 255, 1);
}

.nav-container{
  z-index: 1;
}

.navmodal{
  position: absolute;
}

#navmodal-home{
    opacity: 0;
  animation-name: fade-in;
  animation-duration: 1s;
  animation-delay: 0.2s;
  animation-fill-mode:forwards;
}

#navmodal-about-me{
    opacity: 0;
  animation-name: fade-in;
  animation-duration: 1s;
  animation-delay: 0.3s;
  animation-fill-mode:forwards;
}

#navmodal-projects{
    opacity: 0;
  animation-name: fade-in;
  animation-duration: 1s;
  animation-delay: 0.4s;
  animation-fill-mode:forwards;
}

#navmodal-contact{
    opacity: 0;
  animation-name: fade-in;
  animation-duration: 1s;
  animation-delay: 0.5s;
  animation-fill-mode:forwards;
}


@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}